import React from "react";
import PropTypes from "prop-types";
import PostList from './../components/Blog/PostList.js';

// Components
import Link from "gatsby-link";

const Tags = ({ pathContext, data }) => {
    console.log("Tags tenplate");
  const { tag } = pathContext;
  const { edges, totalCount } = data.allJsonJson;
  const tagHeader = `${totalCount} post${
    totalCount === 1 ? "" : "s"
  } tagged with "${tag}"`;

  return (
    <div>
      <h1>{tagHeader}</h1>
      <ul>
        {edges.map(({ node }) => {
          const { path, value } = node;
          return (
            <li key={path}>
              <Link key={path} to={path}>{value}</Link>
            </li>
          );
        })}
      </ul>
      {/*
              This links to a page that does not yet exist.
              We'll come back to it!
            */}
      <Link key="/tags" to="/tags">All tags</Link>
      <PostList posts={edges} />
    </div>
  );
};

export default Tags;

Tags.propTypes = {
    pathContext: PropTypes.shape({
      tag: PropTypes.string.isRequired,
    }),
    data: PropTypes.shape({
      allJsonJson: PropTypes.shape({
        totalCount: PropTypes.number.isRequired,
        edges: PropTypes.arrayOf(
          PropTypes.shape({
            node: PropTypes.shape({
                value: PropTypes.string.isRequired,
                path: PropTypes.string.isRequired,
                tags: PropTypes.arrayOf({
                    tag: PropTypes.string.isRequired
                })
            }),
          }).isRequired
        ),
      }),
    }),
  };
  

export const pageQuery = graphql`
  query TagPage($tag: String) {
    allJsonJson(
      filter: { tags: { in: [$tag] } }
    ) {
      totalCount
      edges {
        node {
          value
          path
          tags
        }
      }
    }
  }
`;


