import React, { Component } from "react";
import Link from 'gatsby-link';

class PostList extends Component {
    constructor(props){
        super(props);
        console.log("post list");
        console.log(props);
    }
    renderPosts(posts){
        return posts.map(p => {
            let {path, tags, value} = p.node;
            return <div key={path}>
                    <Link to={path} key={path}>Post! Path: {path}, Value: {value}</Link>
                    {/* <a href={path} >{value}</a> */}
                </div>
        })
    }
  render() {
    return (
      <div>
        <p>Post List</p>
        {this.renderPosts(this.props.posts)}
      </div>
    );
  }
}

export default PostList;


